import { createRef, useContext, useEffect, useState } from "react";

import { uiContext } from "../../contexts/UIContext";
import RLogo from "../../assets/R-logo.svg";
import "./Header.css";

const Header = () => { 
    const { mobileMenuActive, setMobileMenuActive } = useContext(uiContext);
    const [ headerHidden, setHeaderHidden ] = useState(false);
    const [lastScrollPosition, setLastScrollPosition] = useState(0);
    const headerRef = createRef();
    const mobileMenuRef = createRef();

    const handleScrollEvent = () => { 
        const bodyOffset = document.body.getBoundingClientRect();
        if(lastScrollPosition < -bodyOffset.top) { 
            setHeaderHidden(true);
        } else { 
            setHeaderHidden(false);
        }
        setLastScrollPosition(-bodyOffset.top);
    };

    const handleClickEvent = e => { 
        mobileMenuActive && !e.target.contains(mobileMenuRef.current)? setMobileMenuActive(false) : null
    }

    useEffect(() => { 
        document.addEventListener("scroll", handleScrollEvent);
        document.addEventListener("click", handleClickEvent);

        return () => { 
            document.removeEventListener("scroll", handleScrollEvent);
            document.removeEventListener("click", handleClickEvent)
        }
    })

    return (
        <header className={`app-header ${headerHidden? "header-hidden" : "header-visible"}`} ref={headerRef}>
            <div className={`app-header__logo ${mobileMenuActive? "item-blured" : "item-not-blured"}`}>
                <img src={RLogo} alt="radu-uivari-logo"></img> 
            </div>
            <nav className="app-header__mobile-navigation">
                <button className="app-header__mobile-navigation__button"
                        onClick={() => setMobileMenuActive(true)}        
                >
                    <div className="app-header__mobile-navigation__button__line"/>
                    <div className="app-header__mobile-navigation__button__line"/>
                    <div className="app-header__mobile-navigation__button__line"/>
                </button>
                <div className={`app-header__mobile-navigation__menu ${mobileMenuActive? "app-header__mobile-navigation__menu--visible" : "app-header__mobile-navigation__menu--hidden"}`}
                     ref={mobileMenuRef}
                >
                    <button className="app-header__mobile-navigation__menu__button" 
                            onClick={() => setMobileMenuActive(false)}
                    >
                        <div className="app-header__mobile-navigation__menu__button__line"/>
                        <div className="app-header__mobile-navigation__menu__button__line"/>
                    </button>
                    <ul className="app-header__mobile-navigation__menu__links">
                        <li className="app-header__mobile-navigation__menu__links__link-item"><a onClick={() => setMobileMenuActive(false)} href="#about">About</a></li>
                        <li className="app-header__mobile-navigation__menu__links__link-item"><a onClick={() => setMobileMenuActive(false)} href="#work">Work</a></li>
                        <li className="app-header__mobile-navigation__menu__links__link-item"><a onClick={() => setMobileMenuActive(false)} href="#blog">Blog</a></li>
                        <li className="app-header__mobile-navigation__menu__links__link-item"><a onClick={() => setMobileMenuActive(false)} href="#contact">Contact</a></li>
                    </ul>
                </div>
            </nav>
            
            <nav className="app-header__main-navigation">
                <ul className="app-header__main-navigation__menu">
                        <li className="app-header__main-navigation__menu-item"><a href="#about">About</a></li>
                        <li className="app-header__main-navigation__menu-item"><a href="#work">Work</a></li>
                        <li className="app-header__main-navigation__menu-item"><a href="#blog">Blog</a></li>
                        <li className="app-header__main-navigation__menu-item"><a href="#contact">Contact</a></li>
                </ul>
            </nav>
        </header>
    )
};

export default Header;